@import '../common/variables.scss';

.footer, 
.footer__wrapper {
  display: flex;
}

.footer {
  background-color: #fff;
  justify-content: center;
}

.footer__wrapper {
  width: 87%;
  flex-direction: column;
  align-items: center;
  padding: 5% 0;
}

.footer__for-tablet {
  display: none;
}

.footer__questions {
  font-size: 1.5rem;
  color: #283645;
  line-height: 1.2;
}

.footer__title {
  font-size: 1.5rem;
  color: #283645;
  line-height: 1.2;
  font-size: 1.125rem;
  font-weight: 700;
}

.footer__contact {
  font-size: 1.5rem;
  color: #d22856;
  line-height: 1.2;
  text-align: center;

  &:hover {
    color: #283645;
    text-decoration: underline;
  }

  &:focus {
    color: #bec2c7;
  }
}

.footer__contacts {
  display: flex;
  flex-direction: column;
}

.footer__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 15px 0;
}

.footer__map {
  position: relative;
}

#map {
  width: 100%;
  background-color: #fff;
}

.footer__map::after {
  width: 100%;
  background-color: #fff;
  content: '';
  position: absolute;
  display: block;
  height: 20%;
  top: -1%;
  -webkit-clip-path: polygon(0 0, 50% 100%, 100% 0);
  clip-path: polygon(0 0, 50% 100%, 100% 0);
  z-index: 3;
}

#map {
  height: 12.5rem;

  iframe {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: $tablet-width) {
  .footer::after {
    bottom: 30%;
  }

  .footer__wrapper {
    width: 94%;
  }

  .footer__contacts {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .footer__item {
    margin: 0 1.5625rem 1.5625rem;
    width: 40%;
  }

  .footer__item--address {
    width: 50%;
    margin: 0 auto;
    padding: 3%;
  }

  .footer__questions {
    font-size: 2.5rem;
  }

  .footer__for-tablet {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 2.1875rem 0 1.25rem;
  }

  .footer__link {
    border-style: solid;
    border-width: .1875rem;
    border-color: #e9ebec;
    border-radius: 50%;
    height: 3.125rem;
    width: 3.125rem;
    background-repeat: no-repeat;
    background-position: 50%;

    &:hover {
      border-color: #283645;
    }
  }

  .footer__social {
    display: flex;
    width: 33%;
    justify-content: space-around;
  }

  .footer__link--twitter {
    background-image: url(./../images/twitter.png);

    &:focus {
      background-image: url(./../images/twitter-focus.png);
    }
  }

  .footer__link--facebook {
    background-image: url(./../images/facebook.png);

    &:focus {
      background-image: url(./../images/facebook-focus.png);
    }
  }

  .footer__link--youtube {
    background-image: url(./../images/youtube.png);

    &:focus {
      background-image: url(./../images/youtube-focus.png);
    }
  }

  .footer__academy {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 22%;

    span {
      margin-right: .3125rem;
    }

    &:hover {
      background-image: url(./../images/academy-hover.png);
      background-repeat: no-repeat;
      background-position: 99% center;

      img {
        display: none;
      }
    }

    &:focus {
      background-image: url(./../images/academy-focus.png);
      background-position: 99% center;
      background-repeat: no-repeat;

      img {
        display: none;
      }
    }
  }

  #map {
    height: 23.4375rem;
  }

  .footer__map::after {
    top: -1px;
  }

  .footer__logo {
    &:hover {
      background-image: url(./../images/logo-hover.png);
      background-repeat: no-repeat;
      width: 150px;
      height: 40px;

      img {
        display: none;
      }
    }

    &:focus {
      background-image: url(./../images/footer-logo-focus.png);
      background-repeat: no-repeat;
      width: 150px;
      height: 40px;
      outline-color: transparent;

      img {
        display: none;
      }
    }
  }

}

@media (min-width: $desktop-width) {
  .footer__wrapper {
    width: 80%;
  }

  .footer__questions {
    font-size: 3.125rem;
  }

  .footer__item {
    width: 33%;
    padding: 0;
    margin: 0;
  }

  .footer__item--address {
    width: 33%;
    margin: 0;
    padding: 0 .25rem;
  }

  .footer__item--phone {
    order: 1;
  }

  .footer__item--mail {
    order: 3;
  }

  .footer__item--address {
    order: 2;
  }

  #map {
    height: 31.25rem;
  }

  .footer__for-tablet {
    margin-top: 2.1875rem;
  }

  .footer__contacts {
    padding-bottom: 3%;
  }
}